
import { useCallback, useState, useRef, useEffect, useId } from "react";
import { Link } from "react-router-dom";
import Markdown from "react-markdown";
import "./chatbot_nexus.css"

const ChatHistoryURL = "http://localhost:8000/api/chat/get_history";  // HTTP
const ChatHistoryConvURL = "http://localhost:8000/api/chat/get_history_conv_k";  // HTTP
const StreamingURL = "ws://localhost:8000/api/ws_nexus";        // WebSocket
const UPLOAD_URL = "http://localhost:8000/api/extract_text"; //HTTP

function SideBar({conv_k_H, setCurrent_conv_k, setCurrentConvTitle}){
    const handleConv_k_H = () => {
        if (conv_k_H && conv_k_H.keys){
            var keys = conv_k_H["keys"];
            var meta = conv_k_H["meta"];
            return keys.map(
                (value, idx) => {
                    var meta_idx, title, date, id;
                    meta_idx = meta[idx];
                    title = meta_idx["title"];
                    date = meta_idx["date"];
                    id = meta_idx["id"];
                    //alert(title);
                    //console.log(value, idx);
                    //console.log(keys);
                    return (
                        <div key={idx} className="div_conv_k_chat" onClick={() => {setCurrent_conv_k(value); setCurrentConvTitle(title)}}>
                            <div>
                                {title}
                            </div>
                            <div>
                                Date: {date}
                                <br />
                                id: {id}
                            </div>
                        </div>
                    )
                }
            )
        }
        return (
            <div>
                 Pas de conversations 
            </div>
        )
    }
    return (
        <div>
            <div>
                <button onClick={() => {setCurrent_conv_k("##new_conv##"); setCurrentConvTitle("Nouvelle conversation")}}>Nouvelle conversation</button>
            </div>
            {handleConv_k_H()}
        </div>
    );
}

function ShowMessages({history, historyCopy, error}){
        //console.log("SM", history, historyCopy);
        const handleMsgs = useCallback(() => {
            if (history && history.length > 0 ){
                return history.map(
                    (value, idx) => {
                        var who, msg, className;
                        [who, msg] = value;
                        who = who.split("__");
                        className = "nexus" === who.at(0) ? "nexus_response" : "user_question";
                        //console.log("classname", className, who);
                        return(
                            <div className={className} key={idx}>
                                <div className="msg_div_ctn"><Markdown>{msg}</Markdown></div>
                            </div>
                        )
                    }
                )
            }
            else if(error && error !== ""){
                if (historyCopy && historyCopy.length > 0){
                    var historyCopy_ = [...historyCopy,  ["error", error]]
                    return historyCopy_.map(
                        (value, idx) => {
                            var who, msg, className;
                            [who, msg] = value;
                            if (who == "error"){
                                return (
                                    <div id="erro_div_msg_chat_nexus">
                                         Erreur : {error}
                                    </div>
                                );
                            }
                            who = who.split("__");
                            className = "nexus" in who ? "nexus_response" : "user_question";
                            return(
                                <div id={className} key={idx}>
                                    <div className="msg_div_ctn"><Markdown>{msg}</Markdown></div>
                                </div>
                            );
                        }
                    )   
                }
                 return (
                        <div id="erro_div_msg_chat_nexus">
                             Erreur : {error}
                        </div>
                    );
                
            }
            else{
                return null;
            }
        }
    , [history, historyCopy])
    return (
        <div id="ctn_div_msg_chat_nexus">
            {handleMsgs()}
        </div>
    );
}

function ChatBotNexus(){
    const username = useRef(null);
    const token = useRef(null);
    const salt = useRef(null);
    const msgShowRef = useRef(null);
    const div_ref = useRef(null);
    const wsRef = useRef(null);
    const [input, setInput] = useState('');  
    const [currentResponse, setCurrentResponse] = useState('');  
    const [source, setSource] = useState('');  
    const [isLoading, setIsLoading] = useState(false); 
    const [useRag, setUseRag] = useState(false); 
    const [error, setError] = useState("");
    const [history, setHistory] = useState([]);
    const [historyCopy, setHistoryCopy] = useState([]);
    const [isReady, setIsReady] = useState(false);
    const [name, setName] = useState("qwen");
    const [conv_k_H, setConv_k_H] = useState({});
    const [current_conv_k, setCurrent_conv_k] = useState("##new_conv##");
    const [currentConvTitle, setCurrentConvTitle] = useState("Nouvelle conversation");
    const [files, setFiles] = useState([]);
    const [textes, setTexte] = useState([]);
    const [upLoading, setUpLoading] = useState(false);
    //const [canFetch, setCanFetch] = useState(false);
    const messageEndRef = useRef(null);
    const [theme, setTheme] = useState('dark');

    const setData = useCallback(() => { 
        username.current = sessionStorage.getItem("username");
        token.current = sessionStorage.getItem("token");
        salt.current = sessionStorage.getItem("salt");
        return (username.current != null && token.current != null && salt.current != null) != false;
    }, [username.current])

    useEffect(
        () => {
            setIsReady(setData());
        }
    , [username.current])

    const scrollToBottom = () => {
        messageEndRef.current?.scrollIntoView({ 
            behavior: "smooth", 
            block: "end" 
        });
    };

    useEffect(() => {
        document.documentElement.setAttribute('data-theme', theme);
    }, [theme]);

    const toggleTheme = () => {
        setTheme(prev => prev === 'dark' ? 'light' : 'dark');
    };

     const handleMessage = (event) => {
        var data = JSON.parse(event.data);
        console.log(data);
        var type = data["type"].toLowerCase();
        var done = data["done"];
        if (type === "eom" && done){
            setInput("");
            get_history();
            get_history_conv_k();
            setCurrentResponse("");
            setSource("");
            setIsLoading(false);
            return;
        }
        else if (!done){
            setInput("");
            if (div_ref.current) {
                div_ref.current.textContent = "";
            }
            setIsLoading(true);
            if(type === "answer"){
                var txt = data["message"];
                setCurrentResponse(prev => prev + txt);
            }
            else if(type == "source"){
                var source = data["message"];
                setSource(source);
            }
            if (current_conv_k == "##new_conv##"){
                setCurrent_conv_k(data["conv_k"]);
            }
            if (!currentConvTitle || currentConvTitle == "Nouvelle Conversation"){
                setCurrentConvTitle(data["title"]);
            }
        }
    }

    useEffect(
        () => {
            if (!isReady) return;
            const ws = new WebSocket(StreamingURL + `?username=${username.current}&token=${token.current}&salt=${salt.current}&care=${false}`);
            ws.onopen = () => {
                get_history();
                get_history_conv_k();
                console.log("Websoket ouvert avec nexus pour ", username.current);
            }
            ws.onclose = () => {
                console.log("Websoket ferm avec nexus pour ", username.current);
            }
            ws.onerror = (err) => {
                console.log(" Websoket erreur avec nexus pour ", username.current, "erreur : ", err.message);
            }
            ws.onmessage = (event) => {
                handleMessage(event);
            }
            wsRef.current = ws;
            get_history();
            return () => {
                if (wsRef.current){
                    wsRef.current.close();
                }
            }
        }
    , [username, token, salt, isReady])

    const get_history_conv_k = async () => {
            try{
                setError("");
                //console.log("username", username.current);
                if (username.current){
                    var request_data = new FormData()
                    request_data.append("username", username.current);
                    request_data.append("salt", salt.current);
                    request_data.append("token", token.current);
                    request_data.append("conv_k", current_conv_k);
                    request_data.append("care", false);
                    console.log(request_data, ChatHistoryURL, current_conv_k);
                    
                    var data = await fetch(
                        ChatHistoryConvURL,
                        {
                            "method": "POST",
                            "body": request_data
                        }
                    )
                    //console.log(data);
                    if (!data.ok){
                        throw new Error(` Erreur, code ${data.status}, texte: ${data.statusText}`);
                    }
                    var json = await data.json();
                    //console.log(json.messages);
                    /*setHistoryCopy(prev => {
                            setHistoryCopy(prev);
                            console.log(prev);
                            var i = json.messages;
                            console.log(i, json.messages);
                            return i;
                        }
                    );*/
                    console.log("conv messages : ",json.messages);
                    //setHistory(json.messages);
                    setHistoryCopy(history);
                    setHistory(json.messages)
                    //console.log(history);
                    
                    return json;
                }
            }
            catch(err){
                console.log(" Erreur : ", err.message, err.stack);
                setHistory([]);
                setError(err.message);
            }
        }
    
    const get_history = async () => {
            try{
                setError("");
                //if (!username.current || !current_conv_k) return;
                //console.log("username", username.current);
                if (username.current){
                    var request_data = new FormData()
                    request_data.append("username", username.current);
                    request_data.append("salt", salt.current);
                    request_data.append("token", token.current);
                    request_data.append("care", false);
                    console.log("get_history", request_data, ChatHistoryURL);
                    
                    var data = await fetch(
                        ChatHistoryURL,
                        {
                            "method": "POST",
                            "body": request_data
                        }
                    )
                    //console.log(data);
                    if (!data.ok){
                        throw new Error(` Erreur, code ${data.status}, texte: ${data.statusText}`);
                    }
                    var json = await data.json();
                    //console.log(json.messages);
                    /*setHistoryCopy(prev => {
                            setHistoryCopy(prev);
                            console.log(prev);
                            var i = json.messages;
                            console.log(i, json.messages);
                            return i;
                        }
                    );*/
                    console.log("ConvHK", json.messages);
                    setConv_k_H(prev => {
                        console.log("Previous : ", prev);
                        return json.messages;
                    });
                    return json;
                }
            }
            catch(err){
                console.log(" Erreur : ", err.message, err.stack);
                setHistory([]);
                setError(err.message);
            }
        }
    
   
    
    useEffect(() => {
        if (!isReady) return;
        get_history();
        get_history_conv_k();

        //const inter = setInterval(() => { get_history_conv_k()}, 30000);
        //return () => clearInterval(inter);
    }, []);
    
    console.log("READY : ", isReady);

    useEffect(() => {
        if (!isReady) return;
        get_history();
        get_history_conv_k();
        const inter = setInterval(() => { get_history(); get_history_conv_k()}, 30000);
        return () => clearInterval(inter);
    }, [isReady, current_conv_k, isLoading, currentConvTitle]);

    useEffect(() => {
        get_history();
    }, [current_conv_k, currentConvTitle, isReady, isLoading])

    useEffect(() => {
        get_history_conv_k();
    }, [current_conv_k, currentConvTitle, isReady, isLoading])

    const sendMessage = () => {
        if (isLoading || !current_conv_k) return;
        setError("");
        setInput(prev => prev.trim());
        console.log(input);
        if(!input){
            return;
        }
        var txt = input.slice();
        if (textes){
            textes.forEach(
                (value) => {
                    txt += "Document 1 : \n" + value;
                }
            );
            setTexte([]);
        }
        var data = JSON.stringify(
            {
                "question": txt,
                "rag": useRag,
                "from": username.current,
                "name": name || "qwen",
                "conv_k": current_conv_k,
                "care": false,
            }
        )
        wsRef.current.send(data);
        setIsLoading(true);
        //setInput("");
        /*if (div_ref.current) {
            div_ref.current.textContent = "";
        }*/
    }
    const handleKeyDown = useCallback((e) => {
        if (e.key === "Enter" && !e.shiftKey){
            sendMessage();
            e.preventDefault();
        }
    }, [sendMessage])

    const idx = useId();

    const upload_func = async () => {
        try{
            if (files){
                const array = Array.from(files);
                var req_data = new FormData();
                array.forEach(
                    (value) => {
                        req_data.append("files", value);
                    }
                );
                setUpLoading(true);
                var data = await fetch(
                    UPLOAD_URL,
                    {
                        "method": "POST",
                        "body": req_data,
                    }
                );
                if (!data.ok){
                    throw new Error(`Erreur dans l'upload ${data.status} : ${data.statusText}`);
                }
                var json = await data.json();
                if (json.success){
                    setFiles([]);
                    alert(" Uplaod russi !");
                }
                if (json.text){
                    setTexte(json.text);
                    console.log(json.text);
                    if (!json.success){
                        alert("Erreur potentiel dans l'upload!");
                    }
                    setFiles([]);
                }
                else{
                    if (!json.success){
                        alert(" Echec de l'upload!");
                    }
                }
                console.log("TEXTES", json);
                return json;
            }
        }
        catch(err){
            alert("  Erreur dans l'upload !");
            console.log("Erreur dans l'upload : ", err.message);
        }
        finally{
            setUpLoading(false);
        }
    }
    return (
        <div>
            {
                isReady ? (
                    <>
                        <button 
                            id="theme-toggle" 
                            onClick={toggleTheme}
                        >
                            {theme === 'dark' ? '' : ''}
                        </button>

                        <div id="root_div_nexus_chat">
                            <div>
                                <SideBar
                                conv_k_H={conv_k_H}
                                setCurrent_conv_k={setCurrent_conv_k}
                                setCurrentConvTitle={setCurrentConvTitle}
                                />
                            </div>
                            
                            <div id="nexus_bienvenue_div">
                                <h2 className="h2_nexus">Chat avec  Nexus</h2>
                            </div>
                            <div>
                                <div ref={msgShowRef} id="ctn_msg_div_nexus">
                                    <h3>{currentConvTitle}</h3>
                                    <ShowMessages 
                                        error={error}
                                        history={history}
                                        historyCopy={historyCopy}
                                        currentResponse={currentResponse}
                                    />
                                    
                                    {/*Quand le message ne commence pas encore, 3 points, Claude m'a fait l'animation */}
                                    {isLoading && !currentResponse && (
                                        <div className="nexus_response">
                                            <div className="msg_div_ctn">
                                                <div className="typing-indicator">
                                                    <span className="star"></span>
                                                    <div className="dots">
                                                        <span></span>
                                                        <span></span>
                                                        <span></span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    )}

                                    {/* Message en cours de gnration */}
                                    {currentResponse && (
                                        <div className="nexus_response" key={idx}>
                                            <div className="msg_div_ctn">
                                                <Markdown>{String(currentResponse)}</Markdown>
                                            </div>
                                            {source && (
                                                <div className="source_div">
                                                    <Markdown>{`Source: ${source}`}</Markdown>
                                                </div>
                                            )}
                                        </div>
                                    )}

                                    {/*<div id="nexus" key={idx}>
                                        <div className="msg_div_ctn"><Markdown>{String(currentResponse)}</Markdown></div>
                                        <div className="source_div"><Markdown>{source ? `Source: ${source}` : ""}</Markdown></div>
                                    </div>*/}
                                    <div ref={messageEndRef} />
                                </div>

                                <div id="message_send_div">
                                    <div id="text_div">
                                        <label className="label_msg_nexus">Message :</label> 
                                        <div
                                            id="textarea_ws_nexus"
                                            ref={div_ref}
                                            suppressContentEditableWarning
                                            contentEditable={!isLoading}
                                            onInput={(e) => {setInput(e.currentTarget.textContent)}}
                                            onKeyDown={(e) => {handleKeyDown(e)}}
                                            placeholder=" Votre message..."
                                        >
                                        </div>
                                    </div>
                                    <div>
                                        <label className="label_msg_nexus">Envoyer un fichier  Nexus :</label> 
                                            <input
                                            type="file"
                                            className="nexus_file_upload"
                                            multiple
                                            onChange={(e) => {console.log(e.target.files); setFiles(e.target.files)}}
                                            />
                                    </div>
                                    <div id="div_btns1">
                                        <button className="btn_msg_chat_nexus" onClick={() => {sendMessage()}} disabled={isLoading}>
                                            {!isLoading ? " Envoyer le message " :  " En cours..."}
                                        </button>
                                        <button className="btn_msg_chat_nexus" onClick={() => {setUseRag(prev => !prev)}}>
                                            {useRag ? "Ne pas utiliser le cours": "Utiliser le cours"}
                                        </button>
                                        <button
                                            className="btn_msg_chat_nexus" onClick={() => {upload_func();}} disabled={upLoading}
                                        >
                                            {!upLoading ? " Envoyer les fichier " :  " En cours..."}
                                        </button>
                                        <button 
                                            className="btn_msg_chat_nexus"
                                            id="scroll_btn"
                                            onClick={scrollToBottom}
                                        >
                                             Scroller
                                        </button>

                                        <div>
                                        
                                            {/*<label>Choisir ma tache : </label>*/}
                                            <br />
                                            <select value={name} onChange={(e) => {setName(e.target.value)}} id="select_select_model">
                                                <option value={"phi"}>Recherche avance</option>
                                                <option value={"qwen"}>Normal</option>
                                                <option value={"gemma"}>Histoires, autres</option>
                                            </select>

                                        </div>
                                        </div>
                                    </div>

                                    
                                </div>
                            </div>
                    </>
                    
                    
                ) : (
                    <div style={{fontSize: "1.2em", textAlign: "center"}}>
                         Veuillez d'abord vous connecter  la <Link to="/login">page de connexion</Link>.
                    </div>
                )
            }
        </div>
    );
}

export default ChatBotNexus;