
import { useState, useRef, useCallback, useEffect } from "react";
import { Link } from "react-router-dom";
import "./page_profile.css"

const PROFILE_IMG_URL = "http://localhost:8000/api/set_profile_img"
const GET_IMG_PROFILE = "http://localhost:8000/api/get_profile_img"

const ProfileImg = ({file, setFile, username, salt, token}) => {

    const [uploadLoading, setUploadLoading] = useState(false);
    const [imgName, setImgName] = useState("");

    const uploadFunc = useCallback(async () => {
        try{
            console.log("sss", file, file.length);
            if (file && file.length > 0 &&  username){
                setUploadLoading(true);
                var form = new FormData();
                form.append("file", file[0]);
                form.append("username", username);
                form.append("token", token);
                form.append("salt", salt);
                var data = await fetch(
                    PROFILE_IMG_URL,
                    {
                        "method": "POST",
                        "body": form
                    }
                )
                if (!data.ok){
                    throw new Error(` Erreur, code ${data.status}, texte: ${data.statusText}`);
                }
                var json = await data.json();
                var s = json.success ? " Upload russi" : " chec de l'upload";
                alert(s);
                console.log(s);
                if (!s.includes("chec")){
                    setFile([]);
                    setImgName(json.path);
                    return json;
                }

                else {
                    throw new Error(` Erreur : ${json.reason}`);
                }
            }
            
        }
        catch (err){
            console.log(" Erreur : ", err.message);
            setFile([]);
        }

        finally{
            setUploadLoading(false);
        }
    }, [username, file])

    const getImgProfile = useCallback(async () => {
        try{
            if (username){
                setUploadLoading(true);
                var form = new FormData();
                form.append("username", username);
                form.append("token", token);
                form.append("salt", salt);
                var data = await fetch(
                    GET_IMG_PROFILE,
                    {
                        "method": "POST",
                        "body": form
                    }
                )
                if (!data.ok){
                    throw new Error(` Erreur, code ${data.status}, texte: ${data.statusText}`);
                }
                var json = await data.json();
                if (json.success){
                    setImgName(json.path);
                    return json;
                }

                else {
                    throw new Error(` Erreur : ${json.reason}`);
                }
            }
            
        }
        catch (err){
            console.log(" Erreur : ", err.message);
            setFile([]);
        }

        finally{
            setUploadLoading(false);
        }
    }, [username])

    useEffect(
        () => {
            getImgProfile();
        }
    , [username])
    console.log(imgName);
    return (
        <div id="root_div_img">
            <div id="profile_img_div">
                {
                    (username && salt && token && imgName) && (
                        <figure>
                            <img className="img_content"alt="Votre photo de profile" src={`http://localhost:8000/api/static/profile_imgs/${imgName}`}/>
                            <figcaption style={{textDecoration: "underline", color: "red"}}>Ma tof de profile !</figcaption>
                        </figure>
                    )
                }
            </div>
            <div id="choice_img_profile">
                <label className="label_div_img_profile">Choississez votre photo de profile :</label>
                <br />
                <input 
                type="file"
                onChange={(e) => {setFile(e.target.files); console.log(e.target.files)}}
                id="img_profile_input"
                className="img_profile_inputs"
                placeholder="Choissisez votre tof"
                />
                <div>
                    <button onClick={uploadFunc} className="btn_img_profile">
                        {uploadLoading ? " En cours..." : " Envoyer la photo"}
                    </button>
                </div>
            </div>
        </div>
    );
}

function Profile(){
    const [file, setFile] = useState([]);
    const username = useRef(null);
    const token = useRef(null);
    const salt = useRef(null);

    const setData = useCallback(() => { 
        username.current = sessionStorage.getItem("username");
        token.current = sessionStorage.getItem("token");
        salt.current = sessionStorage.getItem("salt");
        return (username.current != null && token.current != null && salt.current != null) != false;
    }, [username.current])
    

    return (
        <div>
            {
                setData() ?
                (
                    <div>
                        <ProfileImg
                        file={file}
                        setFile={setFile}
                        username={username.current}
                        salt={salt.current}
                        token={token.current}
                        />
                    </div>
                ) : 
                (
                    <div style={{fontSize: "1.2em", textAlign: "center"}}>
                         Veuillez d'abord vous connecter  la <Link to="/login">page de connexion</Link>.
                    </div>
                )
            }
        </div>
    )
}

export default Profile;