import { Link } from "react-router-dom";
import "./login.css"
import { useState, useEffect } from "react"
import NaviguateBtn from "../naviguate_btn";

const URLlogin = "http://localhost:8000/api/login";

function Login(){
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const [response, setResponse] = useState({});
    const [username, setUsername] = useState("");
    const [email, setEmail] = useState("");
    const [salt, setSalt] = useState("");
    const [password, setPassword] = useState("");
    const [passwordVerify, setPasswordVerify] = useState("");
    const [age, setAge] = useState(15);
    const [theme, setTheme] = useState('dark');

    const toggleTheme = () => {
        setTheme(prev => prev === 'dark' ? 'light' : 'dark');
    };

    useEffect(() => {
            document.documentElement.setAttribute('data-theme', theme);
    }, [theme]);

    const login_function = async function () {
        try{
            setLoading(true);
            setError("");
            var can = (
                password.length >= 8  && password.length < 60 && passwordVerify.length >= 8 &&
                password === passwordVerify  &&
                age > 0  && age < 150 &&
                username &&
                email && email.includes("@") &&
                salt && salt.length >= 25
            );
            if (can){
                var json_data = {
                    "username": username,
                    "salt": salt,
                    "password": password,
                    "age": age,
                    "email": email
                }
                var fetch_data = {
                    "method": "POST",
                    "headers": {"Content-Type": "application/json"},
                    "body": JSON.stringify(json_data)
                }
                var data = await fetch(URLlogin, fetch_data);
                if (!data.ok){
                    throw new Error(`Erreur dans le login, code : ${data.status}, text : ${data.statusText}`);
                }
                setUsername("");
                setPassword("");
                setPasswordVerify("");
                setAge(0);
                setEmail("");
                setSalt("");
                var json = await data.json();
                setResponse(json);
                return json;
            }
            else {
                var msg = "";
                if (!(password.length > 8 && passwordVerify.length > 8)){
                    msg += "\nMot de passe trop courts, au moins 8 caractres !"
                }
                if (!password.length < 60){
                    msg += "\nMot de passe trop long !"
                }
                if (!(password === passwordVerify)){
                    msg += "\nMot de passe incohrents !";   
                }

                if (!(age > 0  && age < 150)){
                    msg += "\nge invalide !";
                }

                if (!(email && email.includes("@"))){
                    msg += "\nEmail invalide !";
                }
                if (!(salt && salt.length >= 25)){
                    msg += "\nSalt invalide !";
                }
                throw new Error(msg);
            }
        }
        catch(err){
            console.log("Erreur : ", err, err.message);
            setError(err);
            setResponse({});
        }
        finally{
            setLoading(false);
        }
    }
    async function Help_login_function() {
        //return null;
        return await login_function();
    }

    const Show_response = function(){
        if (response && response.success){
            sessionStorage.setItem("token", response.token);
            sessionStorage.setItem("username", response.username);
            sessionStorage.setItem("salt", response.salt);
            return (
                <div id="show_result_login_div_response">
                    <div className="div_part_response_login">
                        <span className="span_response_login">tat</span> : <br />
                        <p className="p_response_login">
                            {response.state == "old" ? "Ancien utilisateur" : "Nouveau utilisateur"}
                        </p>
                    </div>

                    <div className="div_part_response_login">
                        <span className="span_response_login">Succs</span>  : <br />
                        <p className="p_response_login">
                            {response.success ? "Russie" : "chec"}
                        </p>
                    </div>

                    <div className="div_part_response_login">
                        <span className="span_response_login">Username</span>  : <br />
                        <p className="p_response_login">
                            {response.username}
                        </p>
                    </div>

                    <div className="div_part_response_login">
                        <span className="span_response_login">Token</span>  : <br />
                        <p className="p_response_login">
                            {response.token}
                        </p>
                    </div>

                    <div className="div_part_response_login">
                        <span className="span_response_login">Salt</span>  : <br />
                        <p className="p_response_login">
                            {response.salt}
                            <strong>  (Veuillez  ne pas le perdre)</strong>
                        </p>
                    </div>
                </div>
            );
        }
        else if (error && error != "" && error.message){
            return (
                <div id="show_result_login_div_err">
                    <strong id="strong_err_login">Une erreur est survenue : </strong><br />
                    <p id="p_err_login">
                        {error.message}
                    </p>
                </div>
            );
        }

        return null;
    }
    return(
        <div id="root_div_login">
            <button 
                id="theme-toggle" 
                onClick={toggleTheme}
                aria-label="Toggle theme"
            >
                {theme === 'dark' ? '' : ''}
            </button>
            <div id="bienvenue_div_login">
                <h2 className="h2_bienvenu_login">
                    Bienvenue !
                </h2>
                <h3 className="h3_bienvenu_login">
                    Connection ou Inscription !
                </h3>
            </div>

            <div id="login_div">
                <h3 className="h3_form_login">Formulaire :</h3>
                <div className="form_row_login">
                    <div className="label_div_login">
                        <label htmlFor="username_input_login">  Votre nom d'utilisateur : </label>
                    </div>
                    <div className="input_div_login">
                        <input 
                        id="username_input_login"
                        className="inputs_login"
                        required
                        type="text"
                        value={username}
                        onChange={(e) => {setUsername(e.target.value)}}
                        minLength={10}
                        />
                    </div>

                    <div className="label_div_login">
                        <label htmlFor="age_input_login">  Votre ge : </label>
                    </div>
                    <div className="input_div_login">
                        <input 
                        className="inputs_login"
                        id="age_input_login"
                        required
                        type="number"
                        value={age}
                        onChange={(e) => {setAge(e.target.value)}}
                        min={15}
                        max={150}
                        />
                    </div>


                    <div className="label_div_login">
                        <label htmlFor="password_input_login">  Votre mot de passe : </label>
                    </div>
                    <div className="input_div_login">
                        <input 
                        required
                        id="password_input_login"
                        className="inputs_login"
                        type="password"
                        value={password}
                        onChange={(e) => {setPassword(e.target.value)}}
                        minLength={8}
                        />
                    </div>

                    <div className="label_div_login">
                        <label htmlFor="password_verify_input_login">  Confirmez le mot de passe : </label>
                    </div>
                    <div className="input_div_login">
                        <input 
                        required
                        type="password"
                        id="password_input_login"
                        className="inputs_login"
                        value={passwordVerify}
                        onChange={(e) => {setPasswordVerify(e.target.value)}}
                        minLength={8}
                        />
                    </div>

                    <div className="label_div_login">
                        <label htmlFor="username_input_login">  Votre email : </label>
                    </div>
                    <div className="input_div_login">
                        <input 
                        required
                        className="inputs_login"
                        type="email"
                        value={email}
                        onChange={(e) => {setEmail(e.target.value)}}
                        minLength={10}
                        />
                    </div>

                    <div className="label_div_login">
                        <label htmlFor="salt_input_login">  Votre salt : </label>
                    </div>
                    <div className="input_div_login">
                        <input 
                        id="salt_input_login"
                        className="inputs_login"
                        required
                        type="password"
                        value={salt}
                        onChange={(e) => {setSalt(e.target.value)}}
                        maxLength={32}
                        />
                    
                    </div>

                    <div id="div_btn_submit_login">
                        <button disabled={loading} onClick={() => {Help_login_function()}} id="btn_submit_login">
                            {loading ? "Chargement..." : "Soumettre le formulaire"}
                        </button>
                    </div>
                    <p>
                        Pas de salt ? Pas de souci, <strong><Link to={"/getsalt"}> obtenez-en un ici.</Link></strong>
                    </p>
                </div>
            </div>

            <Show_response />

            {
                (response && response.success) && (
                    <div id="div_fin_login"> 
                        <h3 className="h3_bienvenu_login">
                            Bonne exprience !!!
                        </h3>
                    </div>
                )
            }

            {/*<div id="nav_btn_login">
                <NaviguateBtn />
            </div>*/}
        </div>
    );
}

export default Login;