import { useCallback, useState, useRef, useEffect, useId } from "react";
import { Link } from "react-router-dom";
import Markdown from "react-markdown";
import "./care.css"

const ChatHistoryURL = "http://localhost:8000/api/chat/get_history";
const ChatHistoryConvURL = "http://localhost:8000/api/chat/get_history_conv_k";
const StreamingURL = "ws://localhost:8000/api/ws_nexus";

function SideBar({conv_k_H, setCurrent_conv_k, setCurrentConvTitle}){
    const handleConv_k_H = () => {
        if (conv_k_H && conv_k_H.keys){
            var keys, meta;
            keys = conv_k_H["keys"];
            meta = conv_k_H["meta"];
            return keys.map(
                (value, idx) => {
                    var meta_idx, title, date, id;
                    meta_idx = meta[idx];
                    title = meta_idx["title"];
                    date = meta_idx["date"];
                    id = meta_idx["id"];
                    return (
                        <div key={idx} className="div_conv_k_chat_care" onClick={() => {setCurrent_conv_k(value); setCurrentConvTitle(title)}}>
                            <div>
                                {title}
                            </div>
                            <div>
                                Date: {date}
                                <br />
                                id: {id}
                            </div>
                        </div>
                    )
                }
            )
        }
        return (
            <div>
                 Pas de conversations 
            </div>
        )
    }
    return (
        <div>
            <div>
                <button onClick={() => {setCurrent_conv_k("##new_conv##"); setCurrentConvTitle("Nouvelle conversation")}}> Nouvelle session</button>
            </div>
            {handleConv_k_H()}
        </div>
    );
}

function ShowMessages({history, historyCopy, error}){
        const handleMsgs = useCallback(() => {
            if (history && history.length > 0 ){
                return history.map(
                    (value, idx) => {
                        var who, msg, className;
                        [who, msg] = value;
                        who = who.split("__");
                        className = "nexus" === who.at(0) ? "nexus_response_care" : "user_question_care";
                        return(
                            <div className={className} key={idx}>
                                <div className="msg_div_ctn"><Markdown>{msg}</Markdown></div>
                            </div>
                        )
                    }
                )
            }
            else if(error && error !== ""){
                if (historyCopy && historyCopy.length > 0){
                    var historyCopy_ = [...historyCopy,  ["error", error]]
                    return historyCopy_.map(
                        (value, idx) => {
                            var who, msg, className;
                            [who, msg] = value;
                            if (who == "error"){
                                return (
                                    <div id="erro_div_msg_chat_care" key={idx}>
                                         Erreur : {error}
                                    </div>
                                );
                            }
                            who = who.split("__");
                            className = "nexus" in who ? "nexus_response_care" : "user_question_care";
                            return(
                                <div className={className} key={idx}>
                                    <div className="msg_div_ctn"><Markdown>{msg}</Markdown></div>
                                </div>
                            );
                        }
                    )   
                }
                 return (
                        <div id="erro_div_msg_chat_care">
                             Erreur : {error}
                        </div>
                    );
                
            }
            else{
                return null;
            }
        }
    , [history, historyCopy])
    return (
        <div id="ctn_div_msg_chat_care">
            {handleMsgs()}
        </div>
    );
}

function CareNexus(){
    const username = useRef(null);
    const token = useRef(null);
    const salt = useRef(null);
    const msgShowRef = useRef(null);
    const div_ref = useRef(null);
    const wsRef = useRef(null);
    const [input, setInput] = useState('');  
    const [currentResponse, setCurrentResponse] = useState('');  
    const [source, setSource] = useState('');  
    const [isLoading, setIsLoading] = useState(false); 
    //const [useRag, setUseRag] = useState(false); 
    const [error, setError] = useState("");
    const [history, setHistory] = useState([]);
    const [historyCopy, setHistoryCopy] = useState([]);
    const [isReady, setIsReady] = useState(false);
    const [conv_k_H, setConv_k_H] = useState({});
    const [current_conv_k, setCurrent_conv_k] = useState("##new_conv##");
    const [currentConvTitle, setCurrentConvTitle] = useState("Nouvelle session");
    const messageEndRef = useRef(null);
    const [theme, setTheme] = useState('dark');
    const MOODS = [
        [" Fatigu", "Je suis puis aujourdhui, jai besoin de repos et de douceur."],
        [" Stress", "Tout mangoisse un peu, aide-moi  retrouver du calme."],
        [" Motiv", "Je suis plein dnergie positive, on fait quoi de beau ?"],
        [" Frustr", "Rien ne va, jai envie de tout casser... coute-moi."],
        [" Dcourag", "Jai le moral dans les chaussettes, parle-moi doucement."],
        [" Concentr", "Je suis focus, donne-moi un dfi ou une ide prcise."],
        [" Surcharg", "Trop de choses dans la tte, aide-moi  trier et respirer."],
        [" En forme", "Je me sens invincible aujourdhui, on pousse les limites ?"],
        [" Difficile", "Cest dur l, jai besoin dtre rconfort."],
        [" Moyen", "Bof, ni bien ni mal... on peut changer a ensemble ?"],
        [" Bien", "Je me sens bien aujourdhui, on fait quoi de sympa ?"]
    ];
    const setData = useCallback(() => { 
        username.current = sessionStorage.getItem("username");
        token.current = sessionStorage.getItem("token");
        salt.current = sessionStorage.getItem("salt");
        return (username.current != null && token.current != null && salt.current != null) != false;
    }, [username.current])

    useEffect(
        () => {
            setIsReady(setData());
        }
    , [username.current])

    const scrollToBottom = () => {
        messageEndRef.current?.scrollIntoView({ 
            behavior: "smooth", 
            block: "end" 
        });
    };

    {/*useEffect(() => {
        scrollToBottom();
    }, [history, currentResponse]);*/}

    useEffect(() => {
        document.documentElement.setAttribute('data-theme', theme);
    }, [theme]);

    const toggleTheme = () => {
        setTheme(prev => prev === 'dark' ? 'light' : 'dark');
    };

    const setInInput = (text) => {
        setInput(
            prev => {
                if (div_ref.current) {
                    div_ref.current.textContent = prev + text;
                }
                return prev + text;
            }
        )
    };

    const handleMessage = (event) => {
        var data = JSON.parse(event.data);
        console.log(data);
        var type = data["type"].toLowerCase();
        var done = data["done"];
        if (type === "eom" && done){
            setInput("");
            get_history();
            get_history_conv_k();
            setCurrentResponse("");
            setSource("");
            setIsLoading(false);
            return;
        }
        else if (!done){
            setInput("");
            if (div_ref.current) {
                div_ref.current.textContent = "";
            }
            setIsLoading(true);
            if(type === "answer"){
                var txt = data["message"];
                setCurrentResponse(prev => prev + txt);
            }
            else if(type == "source"){
                var source = data["message"];
                setSource(source);
            }
            if (current_conv_k == "##new_conv##"){
                setCurrent_conv_k(data["conv_k"]);
            }
            if (!currentConvTitle || currentConvTitle == "Nouvelle session"){
                setCurrentConvTitle(data["title"]);
            }
        }
    }

    useEffect(
        () => {
            if (!isReady) return;
            const ws = new WebSocket(StreamingURL + `?username=${username.current}&token=${token.current}&salt=${salt.current}&care=${true}`);
            ws.onopen = () => {
                get_history();
                get_history_conv_k();
                console.log("WebSocket ouvert avec Care pour ", username.current);
            }
            ws.onclose = () => {
                console.log("WebSocket ferm avec Care pour ", username.current);
            }
            ws.onerror = (err) => {
                console.log(" WebSocket erreur avec Care pour ", username.current, "erreur : ", err.message);
            }
            ws.onmessage = (event) => {
                handleMessage(event);
            }
            wsRef.current = ws;
            get_history();
            return () => {
                if (wsRef.current){
                    wsRef.current.close();
                }
            }
        }
    , [username, token, salt, isReady])

    const get_history_conv_k = async () => {
            try{
                setError("");
                if (username.current){
                    var request_data = new FormData()
                    request_data.append("username", username.current);
                    request_data.append("salt", salt.current);
                    request_data.append("token", token.current);
                    request_data.append("conv_k", current_conv_k);
                    request_data.append("care", true);
                    
                    var data = await fetch(
                        ChatHistoryConvURL,
                        {
                            "method": "POST",
                            "body": request_data
                        }
                    )
                    if (!data.ok){
                        throw new Error(` Erreur, code ${data.status}, texte: ${data.statusText}`);
                    }
                    var json = await data.json();
                    setHistoryCopy(history);
                    setHistory(json.messages)
                    return json;
                }
            }
            catch(err){
                console.log(" Erreur : ", err.message, err.stack);
                setHistory([]);
                setError(err.message);
            }
        }
    
    const get_history = async () => {
            try{
                setError("");
                if (username.current){
                    var request_data = new FormData()
                    request_data.append("username", username.current);
                    request_data.append("salt", salt.current);
                    request_data.append("token", token.current);
                    request_data.append("care", true);
                    
                    var data = await fetch(
                        ChatHistoryURL,
                        {
                            "method": "POST",
                            "body": request_data
                        }
                    )
                    if (!data.ok){
                        throw new Error(` Erreur, code ${data.status}, texte: ${data.statusText}`);
                    }
                    var json = await data.json();
                    setConv_k_H(prev => {
                        return json.messages;
                    });
                    return json;
                }
            }
            catch(err){
                console.log(" Erreur : ", err.message, err.stack);
                setHistory([]);
                setError(err.message);
            }
        }
    
     useEffect(() => {
        get_history();
    }, [current_conv_k, currentConvTitle, isReady, isLoading])

    useEffect(() => {
        get_history_conv_k();
        
    }, [current_conv_k, currentConvTitle, isReady, isLoading])

    useEffect(() => {
        if (!isReady) return;
        get_history();
        get_history_conv_k();
    }, []);
    
    useEffect(() => {
        if (!isReady) return;
        get_history();
        if (current_conv_k !== "##new_conv##") {
            get_history_conv_k();
        }
        
        const inter = setInterval(() => { 
            get_history(); 
            if (current_conv_k !== "##new_conv##") {
                get_history_conv_k();
            }
        }, 30000);
        get_history_conv_k();
        return () => clearInterval(inter);
    }, [isReady, current_conv_k, isLoading]);

    const sendMessage = () => {
        if (isLoading || !current_conv_k) return;
        setError("");
        setInput(prev => prev.trim());
        if(!input){
            return;
        }
        var data = JSON.stringify(
            {
                "question": input,
                "rag": false,
                "from": username.current,
                "name": "gemma",
                "conv_k": current_conv_k,
                "care": true,
            }
        )
        wsRef.current.send(data);
        setIsLoading(true);
    }
    
    const handleKeyDown = useCallback((e) => {
        if (e.key === "Enter" && !e.shiftKey){
            sendMessage();
            e.preventDefault();
        }
    }, [sendMessage])

    const idx = useId();
    return (
        <div>
            {
                isReady ? (
                    <>
                        <button 
                            id="theme-toggle" 
                            onClick={toggleTheme}
                            aria-label="Toggle theme"
                        >
                            {theme === 'dark' ? '' : ''}
                        </button>

                        <div id="root_div_nexus_chat_care">
                            <div>
                                <SideBar
                                    conv_k_H={conv_k_H}
                                    setCurrent_conv_k={setCurrent_conv_k}
                                    setCurrentConvTitle={setCurrentConvTitle}
                                />
                            </div>
                            
                            <div id="nexus_bienvenue_div_care">
                                <h2 className="h2_nexus_care"> Care - Ton espace bien-tre</h2>
                            </div>
                            
                            <div>
                                <div ref={msgShowRef} id="ctn_msg_div_nexus_care">
                                    <h3>{currentConvTitle}</h3>
                                    <ShowMessages 
                                        error={error}
                                        history={history}
                                        historyCopy={historyCopy}
                                        currentResponse={currentResponse}
                                    />
                                    
                                    {isLoading && !currentResponse && (
                                        <div className="nexus_response_care">
                                            <div className="msg_div_ctn">
                                                <div className="typing-indicator_care">
                                                    <span className="star"></span>
                                                    <div className="dots">
                                                        <span></span>
                                                        <span></span>
                                                        <span></span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    )}

                                    {currentResponse && (
                                        <div className="nexus_response_care" key={idx}>
                                            <div className="msg_div_ctn">
                                                <Markdown>{String(currentResponse)}</Markdown>
                                            </div>
                                            {source && (
                                                <div className="source_div_care">
                                                    <Markdown>{`Source: ${source}`}</Markdown>
                                                </div>
                                            )}
                                        </div>
                                    )}

                                    <div ref={messageEndRef} />
                                </div>

                                <div id="message_send_div_care">
                                    <div id="text_div_care">
                                        <label className="label_msg_care"> Message :</label> 
                                        <div
                                            id="textarea_ws_care"
                                            ref={div_ref}
                                            suppressContentEditableWarning
                                            contentEditable={!isLoading}
                                            onInput={(e) => {setInput(e.currentTarget.textContent)}}
                                            onKeyDown={(e) => {handleKeyDown(e)}}
                                            placeholder="Comment te sens-tu aujourd'hui ? "
                                        />
                                    </div>
                                    <div id="div_btns1_care">
                                        <button className="btn_msg_chat_care" onClick={() => {sendMessage()}} disabled={isLoading}>
                                            {!isLoading ? " Envoyer " :  " En cours..."}
                                        </button>
                                        <button 
                                            className="btn_msg_chat_care"
                                            id="scroll_btn"
                                            onClick={scrollToBottom}
                                        >
                                             Scroller
                                        </button>
                                    </div>
                                </div>
                            </div>
                            
                            <div>
                                <div className="mood-selector_care">
                                    {
                                        MOODS.map(
                                            (value, idx) => (
                                                <button 
                                                    className="btn_msg_chat_care" 
                                                    key={idx}
                                                    onClick={() => {setInInput(value[1]);}}
                                                    disabled={isLoading}
                                                >
                                                    {value[0]}
                                                </button>   
                                            )
                                        )
                                    }
                                </div>

                                <div className="quick-actions_care">
                                    <button 
                                        className="btn_msg_chat_care" 
                                        onClick={() => setInInput('J\'aimerais faire un exercice de respiration pour me dtendre')}
                                        disabled={isLoading}
                                    >
                                         Exercice de respiration
                                    </button>
                                    <button 
                                        className="btn_msg_chat_care" 
                                        onClick={() => setInInput('J\'aimerais une musique relaxante pour me calmer')}
                                        disabled={isLoading}
                                    >
                                         Musique relaxante
                                    </button>
                                </div>
                            </div>
                        </div>
                    </>
                ) : (
                    <div style={{fontSize: "1.2em", textAlign: "center"}}>
                         Veuillez d'abord vous connecter  la <Link to="/login">page de connexion</Link>.
                    </div>
                )
            }
        </div>
    );
}

export default CareNexus;