import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./not_found.css";

function NotFound() {
    const [theme, setTheme] = useState('dark');
    const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
    const [clicks, setClicks] = useState(0);
    const navigate = useNavigate();

    useEffect(() => {
        document.documentElement.setAttribute('data-theme', theme);
    }, [theme]);

    useEffect(() => {
        const handleMouseMove = (e) => {
            setMousePosition({ x: e.clientX, y: e.clientY });
        };
        window.addEventListener('mousemove', handleMouseMove);
        return () => window.removeEventListener('mousemove', handleMouseMove);
    }, []);

    const toggleTheme = () => {
        setTheme(prev => prev === 'dark' ? 'light' : 'dark');
    };

    const handleEasterEgg = () => {
        setClicks(prev => prev + 1);
        if (clicks + 1 === 10) {
            alert(" Easter Egg! Tu as trouv le secret de Nexus! ");
            navigate('/');
        }
    };

    const suggestions = [
        { icon: "", name: "Brain", path: "/chat_nexus", desc: "Tuteur IA intelligent" },
        { icon: "", name: "Care", path: "/care", desc: "Bien-tre mental" },
        { icon: "", name: "Accueil", path: "/", desc: "Page principale" },
        { icon: "", name: "Docs", path: "/docs", desc: "Documentation" }
    ];

    return (
        <div className="notfound-container">
            <button 
                id="theme-toggle-404" 
                onClick={toggleTheme}
                aria-label="Toggle theme"
            >
                {theme === 'dark' ? '' : ''}
            </button>

            {/* Background particles suivant la souris */}
            <div 
                className="cursor-glow" 
                style={{ 
                    left: `${mousePosition.x}px`, 
                    top: `${mousePosition.y}px` 
                }}
            />

            <div className="notfound-content">
                {/* 404 anim */}
                <div className="error-code" onClick={handleEasterEgg}>
                    <span className="digit digit-4-1">4</span>
                    <span className="digit digit-0">
                        <div className="orbit">
                            <div className="planet"></div>
                        </div>
                        0
                    </span>
                    <span className="digit digit-4-2">4</span>
                </div>

                {/* Message */}
                <h1 className="error-title">Page Introuvable</h1>
                <p className="error-message">
                    Oups ! On dirait que tu t'es perdu dans l'espace Nexus... 
                    <br />
                    Cette page n'existe pas ou a t dplace.
                </p>

                {/* Astronaute flottant */}
                <div className="astronaut">
                    <div className="astronaut-body">
                        <div className="helmet">
                            <div className="helmet-shine"></div>
                            <div className="face"></div>
                        </div>
                        <div className="body">
                            <div className="arm left-arm"></div>
                            <div className="arm right-arm"></div>
                        </div>
                        <div className="legs">
                            <div className="leg left-leg"></div>
                            <div className="leg right-leg"></div>
                        </div>
                    </div>
                    <div className="rope"></div>
                </div>

                {/* Boutons d'action */}
                <div className="action-buttons">
                    <Link to="/" className="btn-primary-404">
                         Retour  l'accueil
                    </Link>
                    <button onClick={() => window.history.back()} className="btn-secondary-404">
                         Page prcdente
                    </button>
                </div>

                {/* Suggestions */}
                <div className="suggestions-section">
                    <h2 className="suggestions-title">Ou explore ces modules :</h2>
                    <div className="suggestions-grid">
                        {suggestions.map((item, idx) => (
                            <Link to={item.path} className="suggestion-card" key={idx}>
                                <div className="suggestion-icon">{item.icon}</div>
                                <div className="suggestion-info">
                                    <h3>{item.name}</h3>
                                    <p>{item.desc}</p>
                                </div>
                            </Link>
                        ))}
                    </div>
                </div>

                {/* Easter egg hint */}
                <div className="easter-egg-hint">
                    {clicks > 0 && clicks < 10 && (
                        <p className="hint-text">
                             Continue de cliquer sur le 404... ({clicks}/10)
                        </p>
                    )}
                </div>
            </div>

            {/* toiles animes */}
            <div className="stars">
                {[...Array(50)].map((_, i) => (
                    <div 
                        key={i} 
                        className="star"
                        style={{
                            left: `${Math.random() * 100}%`,
                            top: `${Math.random() * 100}%`,
                            animationDelay: `${Math.random() * 3}s`
                        }}
                    />
                ))}
            </div>
        </div>
    );
}

export default NotFound;