import "./naviguation.css";
import { Link, Route, Routes } from "react-router-dom";
import { useState } from "react";
import Login from "./composants/login";
import GetSalt from "./composants/getsalt";
import NotFound from "./composants/not_found";
import Home from "./home";
import Communication from "./composants/websocket";
import Profile from "./composants/page_profile";
import Doc from "./composants/doc";
import ChatBotNexus from "./composants/chatbot_nexus";
import CareNexus from "./composants/care";
import Hub from "./hub";
import MatketPlace from "./composants/market_place";

function Naviguation() {
    const [menuOpen, setMenuOpen] = useState(false);

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const closeMenu = () => {
        setMenuOpen(false);
    };

    return (
        <div>
            <nav>
                {/* Bouton hamburger mobile */}
                <button 
                    className="nav-mobile-toggle" 
                    onClick={toggleMenu}
                    aria-label="Toggle menu"
                >
                    {menuOpen ? '' : ''}
                </button>

                <ul className={menuOpen ? 'open' : ''}>
                    <li>
                        <Link to="/" onClick={closeMenu}>
                             Accueil
                        </Link>
                    </li>
                    <li>
                        <Link to="/chat_nexus" onClick={closeMenu}>
                             Brain
                        </Link>
                    </li>
                    <li>
                        <Link to="/care" onClick={closeMenu}>
                             Care
                        </Link>
                    </li>
                    <li>
                        <Link to="/hub" onClick={closeMenu}>
                             Hub Skills
                        </Link>
                    </li>
                    <li>
                        <Link to="/marketplace" onClick={closeMenu}>
                             Marketplace
                        </Link>
                    </li>
                    <li>
                        <Link to="/ws" onClick={closeMenu}>
                             Messages
                        </Link>
                    </li>
                    <li>
                        <Link to="/profile" onClick={closeMenu}>
                             Profil
                        </Link>
                    </li>
                    <li>
                        <Link to="/docs" onClick={closeMenu}>
                             Docs
                        </Link>
                    </li>
                    <li>
                        <Link to="/login" onClick={closeMenu}>
                             Login
                        </Link>
                    </li>
                    <li>
                        <Link to="/getsalt" onClick={closeMenu}>
                             Salt
                        </Link>
                    </li>
                </ul>
            </nav>

            {/* WRAPPER POUR CONTENU (solution au problme nav fixe) */}
            <div className="content-wrapper">
                <Routes>
                    <Route element={<Home />} path="/" />
                    <Route element={<ChatBotNexus />} path="/chat_nexus" />
                    <Route element={<CareNexus />} path="/care" />
                    <Route element={<Hub />} path="/hub" />
                    <Route element={<MatketPlace />} path="/marketplace" />
                    <Route element={<Communication />} path="/ws" />
                    <Route element={<Profile />} path="/profile" />
                    <Route element={<Doc />} path="/docs" />
                    <Route element={<Login />} path="/login" />
                    <Route element={<GetSalt />} path="/getsalt" />
                    <Route element={<NotFound />} path="*" />
                </Routes>
            </div>
        </div>
    );
}

export default Naviguation;