import "./market_place.css";
import { Link } from "react-router-dom";
import { useState, useRef, useEffect, useCallback } from "react";

const UrlAddProduct = "http://localhost:8000/api/mp/add_product";
const UrlGetProduct = "http://localhost:8000/api/mp/get_products";
const UrlGetProfile = "http://localhost:8000/api/hub/get_profile_img";
const BaseHubImgProfile = "http://127.0.0.1:8000/api/static/HubDir/imgs_profile/";
const BaseHubImgProducts = "http://127.0.0.1:8000/api/static/HubDir/products_img/";

function ShowMyProducts({ products, setProduct, username }) {
    return (
        <div>
            {products && products.length > 0 ? (
                <div>
                    {products.map((value, idx) => {
                        const path_image = value.path_image;
                        const name_user = value.name_user;
                        const user_descr = value.user_descr;
                        const product_name = value.name_product;
                        const product_descr = value.product_descr;
                        const categorie = value.categorie;
                        const price = value.price;
                        const device = value.device;
                        const mail = value.mail;
                        const timestamp = value.timestamp;

                        return (
                            <div key={idx} className="product_mp">
                                <div className="min_div_product">
                                    <span className="product_span_min_mp">Username : </span>
                                    {username}
                                </div>

                                {name_user && (
                                    <>
                                        <div className="min_div_product">
                                            <span className="product_span_min_mp">Vrai nom du vendeur : </span>
                                            {name_user}
                                        </div>
                                        {user_descr && (
                                            <div className="min_div_product">
                                                <span className="product_span_min_mp">Un peu sur lui : </span>
                                                {user_descr}
                                            </div>
                                        )}
                                        {mail && (
                                            <div className="min_div_product">
                                                <span className="product_span_min_mp">Son mail : </span>
                                                {mail}
                                            </div>
                                        )}
                                    </>
                                )}

                                <div className="min_div_product">
                                    <span className="product_span_min_mp">Nom du produit : </span>
                                    {product_name}
                                </div>

                                {product_descr && (
                                    <div className="min_div_product">
                                        <span className="product_span_min_mp">Description : </span>
                                        {product_descr}
                                    </div>
                                )}

                                <div className="min_div_product">
                                    <span className="product_span_min_mp">Catgorie : </span>
                                    {categorie}
                                </div>

                                {path_image ? (
                                    <div className="min_div_product">
                                        <span className="product_span_min_mp">Image : </span>
                                        <img src={`${BaseHubImgProducts}${path_image}`} alt="image du produit" />
                                    </div>
                                ) : (
                                    <div className="min_div_product">Pas d'image pour le produit</div>
                                )}

                                <div className="min_div_product">
                                    <span className="product_span_min_mp">Prix : </span>
                                    {price} {device}
                                </div>

                                {timestamp && (
                                    <div className="min_div_product">
                                        <span className="product_span_min_mp">Ajout le : </span>
                                        {timestamp}
                                    </div>
                                )}
                            </div>
                        );
                    })}
                </div>
            ) : (
                <div>Pas de produit !</div>
            )}
        </div>
    );
}

function Helper() {
    return (
        <div>
            <h3> Aide Marketplace</h3>
            <p> Vendez vos ressources ducatives</p>
            <p> Notes de cours, rsums, exercices</p>
            <p> Prix libres en FCFA</p>
            <p> Contact direct par mail</p>
        </div>
    );
}

function MarketPlace() {
    const username = useRef(null);
    const token = useRef(null);
    const salt = useRef(null);
    const [theme, setTheme] = useState('dark');
    const [imgPath, setImgPath] = useState("");
    const [isReady, setIsReady] = useState(false);
    const [file, setFile] = useState(null);
    const [product_prev, setProduct_prev] = useState([]);
    const [slogan, setSlogan] = useState("");
    const [trust_name, setTrust_name] = useState("");
    const [user_descr, setUser_descr] = useState("");
    const [product_name, setProduct_name] = useState("");
    const [product_descr, setProduct_descr] = useState("");
    const [product_categorie, setProduct_categorie] = useState("Autres");
    const [product_price, setProduct_Price] = useState(0);
    const [price_device, setPrice_device] = useState("FCFA");
    const [mail, setMail] = useState("");
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        document.documentElement.setAttribute('data-theme', theme);
    }, [theme]);

    const toggleTheme = () => {
        setTheme(prev => prev === 'dark' ? 'light' : 'dark');
    };

    const setData = useCallback(() => { 
        username.current = sessionStorage.getItem("username");
        token.current = sessionStorage.getItem("token");
        salt.current = sessionStorage.getItem("salt");
        return (username.current != null && token.current != null && salt.current != null);
    }, []);
    
    useEffect(() => {
        setIsReady(setData());
    }, [setData]);

    const getImgProfile = useCallback(async () => {
        try {
            if (isReady) {
                const form_data = new FormData();
                form_data.append("username", username.current);
                form_data.append("salt", salt.current);
                form_data.append("token", token.current);
                const data = await fetch(UrlGetProfile, {
                    method: "POST",
                    body: form_data,
                });
                if (!data.ok) throw new Error(`${data.status}`);
                const json = await data.json();
                if (json.success) {
                    if (json.path) setImgPath(json.path);
                    if (json.slogan) setSlogan(json.slogan);
                }
            }
        } catch (err) {
            console.log("Erreur getImgProfile : ", err.message);
        }
    }, [isReady]);
    
    const getProducts = useCallback(async () => {
        try {
            if (isReady) {
                const form_data = new FormData();
                form_data.append("username", username.current);
                form_data.append("salt", salt.current);
                form_data.append("token", token.current);
                const data = await fetch(UrlGetProduct, {
                    method: "POST",
                    body: form_data,
                });
                if (!data.ok) throw new Error(`${data.status}`);
                const json = await data.json();
                if (json.products) setProduct_prev(json.products);
            }
        } catch (err) {
            console.log("Erreur getProducts : ", err.message);
        }
    }, [isReady]);

    useEffect(() => {
        if (isReady) {
            getImgProfile();
            getProducts();
        }
    }, [isReady, getImgProfile, getProducts]);

    const sendProduct = useCallback(async () => {
        try {
            if (isReady && parseFloat(product_price) >= 0 && product_name.trim()) {
                setLoading(true);
                
                const form_data = new FormData();
                form_data.append("name_user", trust_name);
                form_data.append("user_descr", user_descr);
                form_data.append("categorie", product_categorie);
                form_data.append("price", parseFloat(product_price));
                form_data.append("device", price_device);
                form_data.append("name_product", product_name);
                form_data.append("product_descr", product_descr);
                form_data.append("salt", salt.current);
                form_data.append("token", token.current);
                form_data.append("username", username.current);
                form_data.append("mail", mail);
                
                if (file) {
                    form_data.append("file", file);
                }
                
                const data = await fetch(UrlAddProduct, {
                    method: "POST",
                    body: form_data,
                });
                
                if (!data.ok) throw new Error(`${data.status}`);
                const json = await data.json();
                
                if (json.success) {
                    alert(" Produit ajout !");
                    await getProducts();
                    // Reset formulaire
                    setProduct_Price(0);
                    setPrice_device("FCFA");
                    setFile(null);
                    setProduct_categorie("Autres");
                    setProduct_name("");
                    setProduct_descr("");
                } else {
                    alert(" chec de l'ajout");
                }
            }
        } catch (err) {
            console.log("Erreur sendProduct : ", err.message);
            alert(" Erreur : " + err.message);
        } finally {
            setLoading(false);
        }
    }, [isReady, product_price, product_name, trust_name, user_descr, product_categorie, 
        price_device, product_descr, mail, file, getProducts]);

    return (
        <div>
            <button 
                id="theme-toggle-home" 
                onClick={toggleTheme}
                aria-label="Toggle theme"
            >
                {theme === 'dark' ? '' : ''}
            </button>

            {isReady ? (
                <div>
                    <div id="div_mp_img_profile">
                        {imgPath ? (
                            <>
                                <img src={`${BaseHubImgProfile}${imgPath}`} alt="Profil" />
                                <div id="slogan_div">
                                    <span className="span_slogan">{slogan || "Ajoutez un slogan"}</span>
                                    {isReady && (
                                        <span className="span_slogan">
                                            crivez-moi : <strong>{username.current}</strong>
                                        </span>
                                    )}
                                </div>
                            </>
                        ) : (
                            <div id="put_img_profil_mp">
                                Ajouter votre photo de profil <Link to={"/hub"}>ici</Link>
                            </div>
                        )}
                    </div>

                    <div id="container_div_main_mp">
                        <div id="div_gauche_mp_add_product">
                            <div className="container_div_mp_edit">
                                <label className="label_mp">Vrai nom</label>
                                <input 
                                    type="text"
                                    className="input_mp"
                                    placeholder="Nom..."
                                    value={trust_name}
                                    onChange={(e) => setTrust_name(e.target.value)}
                                />
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Description pro de vous</label>
                                <input 
                                    type="text"
                                    className="input_mp"
                                    placeholder="Description..."
                                    value={user_descr}
                                    onChange={(e) => setUser_descr(e.target.value)}
                                />
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Nom du produit *</label>
                                <input 
                                    type="text"
                                    className="input_mp"
                                    placeholder="Ex: PC, Souris"
                                    value={product_name}
                                    required
                                    onChange={(e) => setProduct_name(e.target.value)}
                                />
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Description du produit</label>
                                <input 
                                    type="text"
                                    placeholder="Dcrivez votre produit..."
                                    className="input_mp"
                                    value={product_descr}
                                    onChange={(e) => setProduct_descr(e.target.value)}
                                />
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Catgorie *</label>
                                <select 
                                    className="input_mp"
                                    value={product_categorie} 
                                    onChange={(e) => setProduct_categorie(e.target.value)}
                                    required
                                >
                                    <option value="PC">PC</option>
                                    <option value="Informatique">Informatique</option>
                                    <option value="Commercial">Commercial</option>
                                    <option value="Autres">Autres</option>
                                </select>
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Photo du produit</label>
                                <input 
                                    type="file"
                                    className="input_mp"
                                    accept="image/*"
                                    onChange={(e) => setFile(e.target.files[0])}
                                />
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Prix *</label>
                                <input 
                                    type="number"
                                    min="0"
                                    placeholder="0"
                                    className="input_mp"
                                    value={product_price}
                                    onChange={(e) => setProduct_Price(e.target.value)}
                                />
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Devise</label>
                                <select 
                                    className="input_mp"
                                    value={price_device} 
                                    onChange={(e) => setPrice_device(e.target.value)}
                                    required
                                >
                                    <option value="FCFA">FCFA</option>
                                    <option value="USD">USD</option>
                                    <option value="Euro">Euro</option>
                                    <option value="Naira">Naira</option>
                                </select>
                            </div>

                            <div className="container_div_mp_edit">
                                <label className="label_mp">Email de contact</label>
                                <input 
                                    type="email"
                                    placeholder="votre.email@example.com"
                                    className="input_mp"
                                    value={mail}
                                    onChange={(e) => setMail(e.target.value)}
                                />
                            </div>

                            <div className="div_mp_btn">
                                <button 
                                    className="mp_btn"
                                    onClick={sendProduct}
                                    disabled={loading}
                                >
                                    {!loading ? " Ajouter le produit" : " En cours..."} 
                                </button>
                            </div>
                        </div>

                        <div id="div_milieu_mp_mes_produits">
                            <ShowMyProducts 
                                products={product_prev}
                                setProduct={setProduct_prev}
                                username={username.current}
                            />
                        </div>

                        <div id="div_droite_mp_help">
                            <Helper />
                        </div>
                    </div>
                </div>
            ) : (
                <div className="mp-not-logged">
                     <Link to="/login">Connectez-vous</Link> pour accder au Marketplace
                </div>
            )}
        </div>
    );
}

export default MarketPlace;