import "./getsalt.css"

import { useState } from "react"
import NaviguateBtn from "../naviguate_btn";

const SaltURL = "http://localhost:8000/api/salt";

function GetSalt(){
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const [response, setResponse] = useState({});

    const fetchSalt = async function (){
        try{
            setLoading(true);
            setError("");
            const data = await fetch(SaltURL);
            if (!data.ok){
                var errTxt = await data.text();
                throw new Error(`Erreur dans le login, code : ${data.status}, text : ${errTxt | data.statusText}`);
            }
            var json = await data.json();
            setResponse(json);
            return json;
            
        }
        catch(err){
            console.log("Erreur : ", err, err.message);
            setError(err);
            setResponse({});
        }
        finally{
            setLoading(false)
        }
    }

    async function HelpFetch() {
        return await fetchSalt();
    }
    const Show_response = () => {
        if (response && response.salt && response.datetime){
            return (
                <div id="global_div_getsalt">
                    <div className="div_part_getsalt">
                        <label className="label_getsalt"> Salt </label>
                        <span className="span_getsalt"> {response.salt} </span>
                    </div>


                    <div className="div_part_getsalt">
                        <label className="label_getsalt"> Date d'obtention </label>
                        <span className="span_getsalt"> {response.datetime} </span>
                    </div>
                </div>
            );
        }
        else if (error && error != "" && error.message){
            return (
                <div id="err_div_getsalt">
                    <strong id="strong_err_getsalt">Une erreur est survenue : </strong><br />
                    <p id="p_err_getsalt">
                        {error.message}
                    </p>
                </div>
            );
        }

        return null;
    }
    return (
        <div id="div_root_getsalt">
            <h3 id="h3_get_salt">
                    Brve explication concernant le salt :
            </h3>

            <div id="div_explain_salt">
                <p id="explain_salt">
                    Vous vous demandez srement qu'est ce que le <strong>salt</strong>. Pour faire court, c'est juste un petit lment, une suite de 
                    nombre utilis pour la protection de vos donnes. Veuillez donc  ne jamais le perdre. Cliquez sur le bouton pour obtenir votre salt.
                    Merci et bonne continuation !
                </p>
            </div>
            
            <div id="getsalt_btn_div">
                <button disabled={loading} onClick={() => {HelpFetch()}} id="getsalt_btn">
                    {loading ? "Chargement..." : "Obtenir un salt"}
                </button>
            </div>
            
            <Show_response />
        </div>
    );
}

export default GetSalt;